<template id="roof">
  <div>
    <h1>Risk Grading System</h1>
    <input type="text" placeholder="请输入要查询的公司或机构名称" style=" box-sizing: border-box;text-align:center;font-size:1.4em;height:2.7em;border-radius:4px;border:1px solid #c8cccf;color:#6a6f77;-web-kit-appearance:none; -moz-appearance: none;display:block;outline:0;padding:0 1em;text-decoration:none" v-model="msg">
    <button @click="showChildComponentData">评估</button>
    <div v-show="isshow">
      <ul v-for="item in select" :key='item'  style="width: 10%;height:10%;margin:0 auto"><li>{{item}}</li></ul>
    </div>
    <P> 已收录 {{ number }} 家机构 </P>
    <router-link to="/About">About</router-link>
  </div>
</template>

<script type="text/javascript">
import jQuery from 'jquery'
import { getAddr } from '../addresses'

var getname = []
export default {
  data () {
    return {
      isshow: true,
      msg: '',
      films: [],
      select: [],
      number: '19982'
    }
  },
  methods: {
    showChildComponentData: function () {
      this.$store.state.keyword = this.msg
      this.$router.push({path: '/Result'})
    }
  },
  watch: {
    msg: function (val, oldVal) {
      if (val.length === 0) {
        this.isshow = false
      } else {
        var key = this.msg
        jQuery.ajax({
          type: 'post',
          async: false,
          url: getAddr('/servlet/helloWorld'),
          data: {keyword: key},
          dataType: 'json',
          success: (result) => {
            getname = result.map(v => v.name)
            var res = []
            var json = {}
            for(var i = 0; i < getname.length; i++){
              if (!json[getname[i]]) {
                res.push(getname[i])
                json[getname[i]] = 1
              }
              this.select = res
              return
            }
          },
          error: function() {
            console.log('Wrong')
          }
        })
        this.isshow = true
      }
    }
  }
}
</script>

<style scoped>
ul{
  border:1px solid red;
} 
li{
  height:40px;
  line-height: 40px;
  border-bottom: 1px solid #ddd;
}
.bindingbtn input{
  border:1px solid #333;
  height:44px;
  line-height:44px;
}
</style>



