<template id="Result">
  <div>
    <p> 风险评估结果 </p>
    <p>实体名称：{{keyword}}</p>
    <view1></view1>
    <view2></view2>
    <view3></view3>
   </div>
</template>

<script>
export default {
  components: {
    view1: require('./view1.vue').default,
    view2: require('./view2.vue').default,
    view3: require('./view3.vue').default
  },
  computed: {
    keyword () {
      return this.$store.state.keyword
    }
  }
}
</script>

