import Vue from 'vue'
import Router from 'vue-router'
import roof from '@/components/roof'
import About from '@/components/About'
import Result from '@/components/Result'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'roof',
      component: roof
    },
    {
      path: '/About',
      name: 'About',
      component: About
    },
    {
      path: '/Result',
      name: 'Result',
      component: Result
    }
  ]
})



